.portfolioContainer {
  width: 50%;
  float: left;
  position: relative;
  overflow: hidden; }
  .portfolioContainer .inner {
    padding: 2vw;
    position: relative;
    z-index: 2; }
  .portfolioContainer .link {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0; }
  .portfolioContainer:hover .portfolio-foto {
    transform: scale(1); }
    .portfolioContainer:hover .portfolio-foto::after {
      opacity: 0;
      -webkit-transform: translate3d(0, -50px, 0);
      transform: translate3d(0, -50px, 0); }

.portfolioHome .square, .portfolioPage .square {
  height: 100%;
  width: 100%; }
  .portfolioHome .square .portfolio-foto, .portfolioPage .square .portfolio-foto {
    height: 900px; }

.portfolioHome .rect, .portfolioPage .rect {
  height: 50%;
  width: 100%; }
  .portfolioHome .rect .portfolio-foto, .portfolioPage .rect .portfolio-foto {
    height: 450px; }

.portfolio-foto {
  width: 100%;
  height: 100%;
  position: relative;
  left: 0;
  top: 0;
  background-size: cover;
  backface-visibility: hidden;
  width: calc(100% + 50px);
  transition: opacity .4s, transform .4s;
  transform: translate3d(-30px, 0, 0); }
  .portfolio-foto::after {
    content: '';
    position: absolute;
    z-index: 1;
    left: 0;
    width: 100%;
    height: 100%;
    top: 0;
    background: linear-gradient(to bottom, rgba(51, 51, 51, 0.5) 0, rgba(51, 51, 51, 0) 100%);
    transition: opacity .35s,transform .35s;
    opacity: 1; }

